body {
  margin: none;
  height: 1200px;
  text-align: center;
  color: antiquewhite;
  background-image: linear-gradient(to right, rgb(65, 2, 2), rgb(75, 26, 3), rgb(3, 70, 3), rgb(3, 15, 83), rgb(43, 2, 75), rgb(78, 3, 78)) !important;
  font-family: fantasy;
}

button {
  color: rgb(226, 236, 231) !important;
  background-color: rgb(3, 3, 3);
  border: 0px;
  border-radius: 25px;
  text-align: center;
  font-size: larger;
  height: 40px;
  width: 120px;

}
  
 .grid {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 25px;
}
h4 {
  font-size: 0.9rem;
}
img {
  max-height: 350px;
  gap: 15px;
}

